@import "vars";
@import "mixins";
@import "fonts";
@import "settings";
@import "components/header";
.catalog {
    padding: 40px 0 70px 0;
    position: relative;
    background-color: #F5F5FF;
    &__inner {
        display: flex;
    }
    &__aside {
        max-width: 270px;
        margin-right: 30px;
        position: relative;
        // &-img {}
        &-descr {
            font-family: Poppins;
            font-style: normal;
            font-weight: normal;
            font-size: 15px;
            line-height: 23px;
            color: $grey;
            margin-bottom: 30px;
        }
        &-numbers {
            font-family: Poppins;
            font-style: normal;
            font-weight: normal;
            font-size: 18px;
            line-height: 27px;
            color: #333333;
            span {
                font-style: normal;
                font-weight: normal;
                font-size: 40px;
                line-height: 60px;
                color: $blue;
                margin-right: 7px;
            }
            img {
                margin-right: 10px;
            }
        }
    }
    &__tabs {
        display: flex;
        margin-bottom: 35px;
    }
    &__tab {
        font-weight: normal;
        font-size: 18px;
        line-height: 27px;
        position: relative;
        padding-bottom: 10px;
        color: #666666;
        &:hover {
            color: $blue;
            transition: all 0.3s;
        }
        &::before {
            content: "";
            position: absolute;
            width: 0;
            left: auto;
            background-color: #1c62cd;
            height: 3px;
            bottom: -2px;
            transition: all 0.3s;
            right: 0;
        }
        &:hover::before {
            width: 100%;
            right: auto;
            left: 0;
        }
    }
    &__tab+&__tab {
        margin-left: 35px;
    }
    &__item+&__item {
        margin-top: 20px;
    }
    &__item {
        display: flex;
        background: #FFFFFF;
        border: 1px solid #C4C4C4;
        box-sizing: border-box;
        border-radius: 5px;
        &-wrapper {
            display: flex;
        }
        &-sale {
            max-width: 100px;
            font-weight: 500;
            font-size: 34px;
            line-height: 40px;
            text-align: center;
            color: $blue;
            background-color: #5e63ff2f;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
        &-descr {
            padding-left: 20px;
            align-items: center;
        }
        &-title {
            font-weight: 500;
            font-size: 21px;
            line-height: 31px;
            color: #333333;
        }
        &-subdescr {
            font-size: 16px;
            line-height: 24px;
            color: #4D4D4D;
            max-width: 505px;
            a {
                font-size: 16px;
                line-height: 24px;
                text-decoration-line: underline;
                color: #5E63FF;
                &:hover {
                    color: #4d4d4d;
                    transition: all 0.3s;
                }
            }
        }
        &-info {
            padding: 20px 15px 10px 30px;
            text-align: center;
        }
        &-btn {
            min-width: 200px;
            font-weight: 600;
            font-size: 15px;
            line-height: 22px;
            background: $blue;
            color: #FFFFFF;
            padding: 10px 25px;
            border-radius: 4px;
            &:hover {
                background-color: #4d4d4d;
                transition: all 0.3s;
            }
        }
        &-init {
            margin-top: 10px;
        }
        &-coupon {
            font-weight: 500;
            font-size: 15px;
            line-height: 22px;
            color: $blue;
            margin: 0;
            img {
                align-items: center;
            }
        }
        &-used {
            margin: 0;
            font-size: 15px;
            line-height: 22px;
            color: #666666;
        }
    }
}

.catalog__item-notactive {
    opacity: 0.5;
    .catalog__item-info {
        display: none;
    }
}

.stores {
    margin: 50px 0;
    &__title {
        h2 {
            font-weight: 500;
            font-size: 28px;
            line-height: 42px;
            color: #333333;
        }
    }
    &__servises {
        display: flex;
        justify-content: space-between;
    }
    &__item {
        border: 2px solid #5e63ff7b;
        border-radius: 5px;
        transition: 0.3s;
        &-img {
            padding: 20px 18px 8px;
            max-width: 100%;
        }
        &-name {
            font-weight: 500;
            font-size: 16px;
            line-height: 24px;
            text-align: center;
            color: #5E63FF;
        }
        &:hover {
            opacity: 0.7;
            transition: 0.7s;
        }
    }
    &__item+&__item {
        margin-left: 20px;
    }
}

.about {
    margin-bottom: 100px;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    &__title {
        margin-bottom: 30px;
        font-weight: 500;
        font-size: 28px;
        line-height: 42px;
        color: #333333;
    }
    &__text {
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;
        color: #4D4D4D;
    }
}

.footer {
    margin-bottom: 50px;
    &__logo {
        cursor: pointer;
    }
    &__list {
        display: flex;
        margin-bottom: 60px;
        justify-content: space-between;
        align-items: center;
    }
    .footer__item-title {
        font-weight: 500;
        font-size: 15px;
        line-height: 22px;
        color: #4D4D4D;
        margin-bottom: 20px;
    }
    .footer__list-column {
        li a {
            font-size: 15px;
            line-height: 30px;
            color: #4D4D4D;
            transition: all 0.3s;
            &:hover {
                transition: all 0.3s;
                color: #5e63ff;
            }
        }
    }
    &__bottom {
        display: flex;
        justify-content: space-between;
    }
    &__social-list {
        display: flex;
    }
    &__social-img {
        display: flex;
    }
    &__social-item+&__social-item {
        margin-left: 15px;
    }
}

.burger {
    width: 40px;
    height: 20px;
    cursor: pointer;
    background-color: transparent;
    border: none;
    position: relative;
    display: none;
}

.burger::before,
.burger::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 2px;
    background-color: #000;
}

.burger::after {
    top: auto;
    bottom: 0;
}

.burger span {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    height: 2px;
    display: inline-block;
    background-color: #000;
}

.overlay {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 99;
    opacity: 0;
    visibility: hidden;
}

.lock {
    overflow: hidden;
}

.rightside-menu {
    position: fixed;
    overflow: hidden;
    left: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    max-height: 100vh;
    z-index: 999;
    background-color: #fff;
    scroll-behavior: none;
    -webkit-transition: all 0.3s ease-in;
    -o-transition: all 0.3s ease-in;
    transition: all 0.3s ease-in;
    -webkit-transition: all 1s ease;
    -o-transition: all 1s ease;
    transition: all 1s ease;
    -webkit-transform: translateX(100%);
    -ms-transform: translateX(100%);
    transform: translateX(100%);
    opacity: 0.99;
    /* Полупрозрачный фон */
    filter: alpha(Opacity=99);
    border: 10px solid #29292960;
}

.menu-up {
    -webkit-transition: all 1s ease;
    -o-transition: all 1s ease;
    transition: all 1s ease;
    -webkit-transform: translateX(0%);
    -ms-transform: translateX(0%);
    transform: translateX(0%);
    padding-top: 40px;
}

@media (max-width: 1050px) {
    .catalog__inner {
        display: block;
    }
    .catalog__aside {
        max-width: 100%;
        padding-bottom: 40px;
        position: relative;
    }
    .catalog__item {
        justify-content: space-between;
    }
    .catalog__item-info {
        margin: auto 0;
        padding: 0 15px 0 30px;
        text-align: center;
    }
}

@media (max-width: 921px) {
    .header__nav {
        display: none;
    }
    .stores__servises {
        flex-wrap: wrap;
    }
    .stores__item {
        max-width: 178px;
        margin: 10px;
    }
    .stores__item+.stores__item {
        margin-left: 0px;
    }
    .footer__item {
        min-width: 160px;
        margin: 10px 20px;
    }
    .footer__list {
        display: flex;
        margin-bottom: 50px;
        justify-content: none;
        flex-wrap: wrap;
        justify-content: flex-start;
    }
    .header__burger {
        display: block;
        position: relative;
        width: 30px;
        height: 20px;
        transition: all 0.3s;
        cursor: pointer;
        &::after,
        &::before {
            content: '';
            position: absolute;
            background-color: #1c62cd;
            width: 100%;
            height: 2px;
            right: 0;
            transition: all 0.3s;
        }
        &::before {
            top: 0;
            transition: all 0.3s;
        }
        &::after {
            bottom: 0;
            transition: all 0.3s;
        }
        span {
            position: absolute;
            background-color: #1c62cd;
            left: 0;
            width: 100%;
            height: 2px;
            top: 9px;
        }
    }
    .stores__servises {
        justify-content: space-around;
    }
    .menu__logo {
        margin: 0 auto 30px auto;
    }
    .header__burger-menu {
        margin-top: 80px;
        display: block;
        position: relative;
        width: 30px;
        height: 20px;
        margin: 30px auto;
        transition: all 0.3s;
        cursor: pointer;
        &::after,
        &::before {
            content: '';
            position: absolute;
            background-color: #1c62cd;
            width: 100%;
            height: 2px;
            right: 0;
            transition: all 0.3s;
        }
        &::before {
            transform: rotate(45deg);
            top: 9px;
            transition: all 0.3s;
            cursor: pointer;
        }
        &::after {
            transform: rotate(-45deg);
            bottom: 9px;
            transition: all 0.3s;
            cursor: pointer;
        }
        span {
            transform: scale(0);
            transition: all 0.3s;
        }
    }
    .rightside-menu__list {
        text-align: center;
    }
    .rightside-menu__list__item+.rightside-menu__list__item {
        margin-top: 35px;
    }
    .rightside-menu__list-link {
        font-size: 22px;
        line-height: 30px;
        letter-spacing: -0.204545px;
        padding: 10px;
        color: #212a2f;
        &:hover {
            transition: all 0.3s;
            color: #ddc617;
        }
        img {
            display: inline-block;
        }
    }
    .header__logo {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 50%;
    }
    .catalog__item-wrapper {
        display: block;
        width: 100%;
    }
    .catalog__item-info {
        margin: auto 0;
        padding: 10px 15px 10px 30px;
        text-align: center;
    }
    .catalog__tabs {
        display: block;
    }
    .catalog__tab+.catalog__tab {
        margin-left: 0;
        padding: 10px 0;
    }
    .catalog__tab {
        font-weight: normal;
        font-size: 21px;
        line-height: 33px;
        position: relative;
        padding-bottom: 10px;
    }
    .catalog__item-btn {
        margin: 0 auto;
    }
}

@media (max-width: 500px) {
    .header {
        .header__inner {
            display: block;
            text-align: center;
        }
        .header__widget {
            justify-content: space-between;
            padding-bottom: 20px;
        }
    }
    .catalog__tab {
        text-align: center;
    }
    .header__logo {
        width: auto;
    }
    .footer__bottom {
        display: block;
        text-align: center;
    }
    .footer__social-list {
        margin-top: 15px;
        justify-content: center;
    }
}

@media (max-width: 370px) {
    .catalog {
        .catalog__item-btn {
            min-width: 0;
            font-size: 12px;
            line-height: 18px;
            padding: 10px 15px;
            color: #fff;
        }
    }
    .catalog__item-sale {
        min-width: 55px;
        font-size: 22px;
        line-height: 30px;
    }
    .stores__servises {
        justify-content: center;
    }
}